<template>
  <div>
    <el-row>
      <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
        <h1 class="pageTitle">{{ this.pageTitle }}</h1>
      </el-col>
      <el-col id="listData" v-loading="this.loading" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
        <el-row>
          <el-col :span="24">
            <el-popconfirm
              confirmButtonText="Excluir"
              cancelButtonText="Cancelar"
              title="Você tem certeza que deseja excluir os contatos selecionados?"
              @onConfirm="deleteDataModule()"
            >
              <el-button slot="reference" type="primary">Excluir</el-button>
            </el-popconfirm>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-table
              :data="tableData"
              style="width: 100%"
              @selection-change="handleSelectionChange"
              @row-click="linkRowTable"
            >
              <el-table-column type="selection" width="55"></el-table-column>
              <el-table-column prop="cf_nome" label="Nome"></el-table-column>
              <el-table-column prop="cf_email" label="E-mail"></el-table-column>
              <el-table-column prop="cf_fone" label="Telefone"></el-table-column>
              <el-table-column width="70">
                <template slot-scope="scope">
                  <router-link :to="linkToView(scope.row.id)">
                    <el-button size="small">
                      <i class="el-icon-right"></i>
                    </el-button>
                  </router-link>
                </template>
              </el-table-column>
            </el-table>
          </el-col>
        </el-row>
        <el-row id="paginationList">
          <el-col :span="24" v-if="this.pagination()">
            <el-pagination
              background
              layout="prev, pager, next"
              :total="1000"
              :page-size="this.pageSize"
              :current-page="this.currentPage"
            ></el-pagination>
          </el-col>
        </el-row>
      </el-col>
      <el-col :xs="24" :sm="24" :md="6" :lg="6" :xl="6"></el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "Contatos",
  data() {
    return {
      pageSize: 0,
      pages: 0,
      currentPage: 0,
      countTotalContatos: 0,
      pageTitle: "Contatos",
      loading: true,
      tableData: [],
      multipleSelection: [],
    };
  },
  head: {
    title: {
      inner: "Contatos",
      complement: "GuiaSales",
    },
  },
  methods: {
    linkToView(id) {
      return "/dashboard/contacts/" + id;
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    linkRowTable(index) {
      this.$router.push({ name: "Contact_View", params: { id: index.id } });
    },
    pagination() {
      if (this.pages > 1) {
        return true;
      } else {
        return false;
      }
    },
    loadData() {
      this.$http
        .get(
          "/module/contacts/view?fields=cf_nome,cf_sobrenome,cf_email,cf_fone"
        )
        .then((res) => {
          this.loading = false;
          this.tableData = res.data.data;
          this.pages = res.data.meta.total_pages;
          this.currentPage = res.data.meta.page;
          this.pageSize = res.data.meta.per_page;
          this.countTotalContatos = res.data.meta.total_leads;
        })
        .catch((error) => {
          error;
          this.loading = false;
        });
    },
    deleteDataModule() {
      if( this.multipleSelection.length == 0 ){
        this.$message({
          message: 'Selecione ao menos 1 registro',
          type: 'warning'
        });
        return false;
      }
      this.$http
        .post("/module/contacts/bulk", this.multipleSelection)
        .then((res) => {
          res;
          this.loadData();
          this.$notify.success({
            title: "Sucesso",
          });
        })
        .catch((err) => {
          err;
          this.$notify.error({
            title: "Houve um erro ao deletar",
          });
        });
    },
  },
  mounted() {
    this.loadData();
  },
};
</script>

<style lang="scss">
#listData {
  background-color: #fff;
  min-height: 80vh;
  max-height: 80vh;
  height: 80vh;
  overflow-y: auto;
  box-sizing: border-box;
  padding: 15px;

  .el-table {
    margin-top: 30px;

    tr {
      cursor: pointer;
    }
  }

  #paginationList {
    text-align: center;
    margin-top: 50px;
  }
}
</style>